.title {
  text-decoration: none;
  text-align: center;
  font-family: "Courgette", cursive;
  font-size: 8vw;
  color: #6dc9e0;
  text-shadow: 0.5px 0.5px whitesmoke;
}
.title :hover {
  color: aqua;
}

.about-content {
  color: #0f0f0f;
}
.light-mode {
  display: flex;
  align-items: flex-end;
  margin-left: 20px;
}

.button-container {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: center;
}

.btn {
  min-width: 160px;
  height: 35px;
  padding: 2px;
  margin-left: 5px;
  margin-top: 10px;
  color: whitesmoke;
  border-radius: 30px;
  border: 2px solid whitesmoke;
}
.btn:hover {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  height: 35px;
  padding: 2px;
  color: #6dc9e0;
  border-radius: 30px;
  border: 2px solid #6dc9e0;
}

